}

.loader-text {
  margin: 20px;
  font-size: 26px;
  font-weight: 700;
  line-height: 1.07;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid rgba(0, 0, 0, 0);
  border-color: #de3500 rgba(0, 0, 0, 0) #fff;
  border-radius: 50%;
  margin-right: 30px;
  animation: rotate 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  border: 10px solid rgba(0, 0, 0, 0);
  border-bottom-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-10px, 19px)rotate(-35deg);
}

.loader:after {
  border-color: #de3500 rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
  transform: translate(32px, 3px)rotate(-35deg);
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

